<template>
  <div id='content'>
    <input name='query' v-model='searchQuery' placeholder='Search talks by name'>
    <div
      v-for='message in messages'
      v-bind:key='message.id'
      v-bind:class="(message.user_name.indexOf(searchName) != -1 || searchName === null) ? 'searched' : 'unsearched'"
    >
      <div id='message_content' class='md-elevation-1 message_content'>
        <div>
          {{ message.text }}
        </div>
        <div>
        {{ message.user_name }}
        </div>
      </div>
    </div>
  </div>  
</template>

<script>
import 'vue-material/dist/vue-material.min.css'
import axios from 'axios'
import lodash from 'lodash'

export default {
  props: {
  },
  data() {
    return {
      messages: [],
      searchName: null,
      searchQuery: ''
    }
  },
  created: function () {
      this.get_messages(),
        this.debouncedGetAnswer = _.debounce(this.searchMemberTalk, 500)
    },
    methods: {
      get_messages() {
        // TODO:async awaitで書き直す
        axios.get('api/messages')
          .then(res => {
            console.log(res.data)
            this.messages = res.data
          })
      },
      searchMemberTalk() {
        name = this.searchQuery
        if (name === '') {  // inputに何も文字がない場合は、全部の会話を表示させる
          this.searchName = null
        } else {
          this.searchName = this.searchQuery
        }
      }
    },
    watch: {
      searchQuery: function () {
        console.log(this.searchQuery)
        this.debouncedGetAnswer()
      }
    }
}
</script>

<style lang="css">
  .unsearched {
    display: none;
  }
  .message_content {
    margin: 50px;
    text-align: center;
  }
</style>
